import { fromEvent } from 'rxjs';
import { filter } from 'rxjs/operators';
import './styles.scss';

class Point {
  x: number;
  y: number;

  constructor(x: number, y: number) {
    this.x = x;
    this.y = y;
  }

  static isEqual(pointA: Point, pointB: Point) {
    return pointA.x === pointB.x && pointA.y === pointB.y;
  }
}

class Direction {
  xD: number;
  yD: number;
  code: string;

  constructor(xD?: number, yD?: number, code?: string) {
    this.xD = xD || 0;
    this.yD = yD || 0;
    this.code = code || '';
  }
}

class Field {
  canvas: any;
  ctx: any;
  rows: number;
  columns: number;
  cellSize: number;
  canvasWidth: number;
  canvasHeight: number;
  fruit: Point | null = null;

  constructor(rows: number, columns: number, cellSize: number, canvas: any) {
    this.canvas = canvas;
    this.ctx = canvas.getContext('2d');
    this.rows = rows;
    this.columns = columns;
    this.cellSize = cellSize;
    this.canvasWidth = columns * cellSize;
    this.canvasHeight = rows * cellSize;
    this.canvas.width = this.canvasWidth;
    this.canvas.height = this.canvasHeight;
  }

  generateFruit(snake: Snake) {
    let fruit: Point = null;
    let isEqual = false;

    if (!snake || this.fruit) {
      return;
    }

    do {
      fruit = this.getRandomPoint();

      const isEqual = snake.body.every((point) => {
        return point.x !== fruit.x || point.y !== fruit.y;
      });
    } while (isEqual);
    this.fruit = fruit;
  }

  drawFruit(snake: Snake) {
    if (!this.fruit) {
      this.generateFruit(snake);
    }
    this.ctx.fillStyle = 'pink';
    this.ctx.fillRect(
      this.fruit.x,
      this.fruit.y,
      this.cellSize,
      field.cellSize
    );
  }
  getRandomPoint() {
    const rX = Math.floor(Math.random() * this.columns);
    const rY = Math.floor(Math.random() * this.rows);
    return new Point(rX * this.cellSize, rY * this.cellSize);
  }

  drawGrid() {
    const { ctx, columns, rows, canvas, cellSize } = this;
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.beginPath();
    ctx.strokeStyle = 'white';
    ctx.lineWidth = 0.1;
    for (let x = 0; x <= columns; x++) {
      ctx.moveTo(x * cellSize, 0);
      ctx.lineTo(x * cellSize, rows * cellSize);
    }
    for (let y = 0; y <= rows; y++) {
      ctx.moveTo(0, y * cellSize);
      ctx.lineTo(columns * cellSize, y * cellSize);
    }
    // for your original question - you need to stroke only once
    ctx.stroke();
  }
  drawSnake(snake: Snake) {
    if (!snake) {
      return;
    }
    const field = this;
    field.ctx.fillStyle = snake.color;
    if (!snake || snake.body.length === 0) {
      return;
    }
    snake.body.forEach(function (item) {
      field.ctx.fillRect(item.x, item.y, field.cellSize, field.cellSize);
    });
  }
  eatFruit() {
    this.fruit = null;
  }
}

class Snake {
  color: string;
  body: Point[];
  direction: Direction;
  isDead: boolean;

  constructor(startPoint: Point, color?: string, direction?: Direction) {
    this.color = color || 'green';
    this.body = [startPoint || new Point(0, 0)];
    this.direction = direction || new Direction();
    this.isDead = false;
  }

  move(field: Field) {
    if (!field || !this.body || this.body.length < 1 || !this.direction) {
      return;
    }

    const { rows, columns, cellSize } = field;
    const head = this.body[0];
    const newHeadPosition = new Point(head.x, head.y);

    //Calc new x position
    if (head.x === columns * cellSize) {
      newHeadPosition.x = 0;
    } else if (head.x === 0 && this.direction.xD < 0) {
      newHeadPosition.x = (columns - 1) * cellSize;
    } else {
      newHeadPosition.x += this.direction.xD * cellSize;
    }

    //Calc new y position
    if (head.y === rows * cellSize) {
      newHeadPosition.y = 0;
    } else if (head.y === 0 && this.direction.yD < 0) {
      newHeadPosition.y = (rows - 1) * cellSize;
    } else {
      newHeadPosition.y += this.direction.yD * cellSize;
    }

    //Check for collision to be at new position
    const isCollision = snake.body.some((item) => {
      return Point.isEqual(item, newHeadPosition);
    });

    if (isCollision) {
      return;
      console.log('COLLISION-------------------------');
    }

    //Move a
    snake.body.unshift(newHeadPosition);

    //Check for Food to be eaten at new position
    if (!field.fruit || !Point.isEqual(field.fruit, newHeadPosition)) {
      const lastPosition = snake.body.pop();
      //TODO: bad code
      field.ctx.lineWidth = 0.1;
      field.ctx.fillStyle = '#3e3e3e';
      field.ctx.strokeRect(
        lastPosition.x,
        lastPosition.y,
        field.cellSize,
        field.cellSize
      );
      field.ctx.fillStyle = 'white';
      field.ctx.clearRect(
        lastPosition.x,
        lastPosition.y,
        field.cellSize,
        field.cellSize
      );
      field.ctx.strokeRect(
        lastPosition.x,
        lastPosition.y,
        field.cellSize,
        field.cellSize
      );
    } else {
      field.eatFruit();
    }
  }

  changeDirection(newDirection: string) {
    const dir = this.direction;
    const bodyGrown = this.body.length > 1;

    if (newDirection === dir.code) {
      return;
    }
    switch (newDirection) {
      case 'Up':
        if (!bodyGrown || dir.code !== 'Down') {
          dir.xD = 0;
          dir.yD = -1;
          dir.code = 'Up';
        }
        break;
      case 'Down':
        if (!bodyGrown || dir.code !== 'Up') {
          dir.xD = 0;
          dir.yD = 1;
          dir.code = 'Down';
        }
        break;
      case 'Left':
        if (!bodyGrown || dir.code !== 'Right') {
          dir.xD = -1;
          dir.yD = 0;
          dir.code = 'Left';
        }
        break;
      case 'Right':
        if (!bodyGrown || dir.code !== 'Left') {
          dir.xD = 1;
          dir.yD = 0;
          dir.code = 'Right';
        }
        break;
    }
  }
}

class Game {
  field: Field;
  snake: Snake;
  gamepads: number[];
  constructor(field: Field, snake: Snake) {
    this.field = field;
    this.snake = snake;
  }

  init() {
    const game = this;
    this.field.drawGrid();
    this.field.drawSnake(this.snake);
    this.field.drawFruit(this.snake);

    const gameStartHandler = (event: any) => {
      window.removeEventListener('keydown', gameStartHandler);
      const direction = event.key.replace('Arrow', '');
      this.snake.changeDirection(direction);
      game.start();
    };

    window.addEventListener('keydown', gameStartHandler);

    const gamepadHandler = (event: any, connecting: any) => {
      var gamepad = event.gamepad;

      if (connecting) {
        this.gamepads[gamepad.index] = gamepad;
      } else {
        delete this.gamepads[gamepad.index];
      }
    };

    window.addEventListener(
      'gamepadconnected',
      function (e) {
        debugger;
        console.log(e);
        gamepadHandler(e, true);
      },
      false
    );
    window.addEventListener(
      'gamepaddisconnected',
      function (e) {
        gamepadHandler(e, false);
      },
      false
    );
  }

  start() {
    this.watchMoves();
    window.setInterval(() => {
      this.snake.move(this.field);

      this.updateScore();
      if (!this.field.fruit) {
        this.field.drawFruit(this.snake);
      }
      this.field.drawSnake(this.snake);
    }, 100);
  }

  updateScore() {
    const scoreElement = document.getElementById('score');

    if (!this.snake || !scoreElement) {
      return;
    }
    scoreElement.innerHTML = `${this.snake.body.length - 1}`;
  }

  gameOver() {
    alert(`Game over, you score is - ${this.snake.body.length - 1}`);
  }

  watchMoves() {
    window.addEventListener('keydown', (event) => {
      const direction = event.key.replace('Arrow', '');
      this.snake.changeDirection(direction);
    });
  }
}

const canvas = document.querySelector('.canvas');
const field = new Field(30, 30, 20, canvas);
const snake = new Snake(field.getRandomPoint());
const game = new Game(field, snake);

game.init();

